import React, { Component } from 'react';
import InputField from './InputField'
class Register extends Component {
    render() {
        return (
            <div class="horizontal-center textS">
                <p class="title">
                    Register
                </p>
                <div class="form-register-center">
                    <form class="register form-register-center" id="register" autoComplete="off">
                        <InputField text="ชื่อจริง" />
                        <InputField text="นามสกุล" />
                        <InputField text="อีเมล" />
                        <InputField text="บัญชีผู้ใช้" />
                        <InputField text="รหัสผ่าน" />
                        <div class="row-flex border">
                            <div class="register-label-box border">
                                <label class="register top">
                                    ที่อยู่
                                </label>
                            </div>
                            <div class="fullWidth row-flex border register-center">
                                <textarea class="fullWidth" type="text" />
                            </div>                            
                        </div>
                    </form>
                </div>
                <button class="textS login" type="submit" form="register">
                    ยืนยัน
                </button>

            </div >
        );
    }
}

export default Register;